.button {
    padding: 13px 20px;
    border: none;
    border-radius: 2px;
    text-transform: uppercase;
    font-family: 'FuturaDemiC';
    &:hover {
         cursor: pointer; }
    &--primary {
        color: #fff;
        box-shadow: 0px 5px 15px rgba($primary, 0.2s);
        transition: box-shadow 0.3s;
        background-color: $primary; }
    &:hover {
            box-shadow: none; }
    &--outline {
        border: 1px solid #ffffff;
        background-color: transparent;
        color: #ffffff; } }
